import React, { Component } from 'react';

import { Modal, Button,Input, Form } from 'antd';

class NewUserNameForm extends Component {
    constructor(props){
        super(props);

    }

    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        this.props.form.validateFields((err, values) => {
            if (!err) {
                that.props.handleSubmitModUser(values);
            }
        })
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        const { visible,newUserName} = this.props.data;
        const formItemLayout = {
            labelCol: {
              xs: { span: 4 },
              sm: { span: 4 },
            },
            wrapperCol: {
              xs: { span: 20 },
              sm: { span: 20 },
            },
          };

        return (
            <div>
                    {/* 自定义服务设施弹出框 */}
                    <Modal
                        visible={visible}
                        title="修改登录账户"
                        onCancel={this.props.handleUserModalCancel}
                        width="600px"
                        className="copyStoreModal"
                        centered={true}
                        footer={[
                            <Button key="back" onClick={this.props.handleUserModalCancel}>取消</Button>,
                            <Button key="submit" type="primary" onClick={this.handleSubmit}>
                            确定
                            </Button>,
                        ]}
                        >
                        <div >
                        <Form onSubmit={this.handleSubmit}  {...formItemLayout} >
                            <Form.Item>
                            {getFieldDecorator('newUserName', {
                                    rules: [{required:true,message:'请输入新账户名！'},
                                        { pattern: /^[\w-\u4e00-\u9fa5]{1,40}$/, message: '请输入正确的账户名！!' },
                                    ],
                                    initialValue:newUserName,
                                    validateFirst:true,
                                })(
                                    <Input placeholder="请输入新账户名"></Input>
                                )}
                            </Form.Item>
                        </Form>
                        </div>
                    </Modal>


            </div>
        )
    }

}

export default Form.create({ name: 'describe' })(NewUserNameForm)