import React, { Component } from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {getStorage} from '../../config/mUtils'
import {Layout} from 'antd';
import '../../style/index.scss';

import SiderCustom from '../common/SiderCustom';
import HeaderCustom from '../common/HeaderCustom';

import storeManagement from './storeManagement/StoreManagement';
import goodsManagement from './goodsManagement/GoodsManagement';
import toggleDish from './goodsManagement/toggleDish/ToggleDish';
import goodsManagement1 from './goodsManagement/GoodsManagement.1';
import sellManagement from './sellManagement/SellManagement';
import ToggleCoupon from './sellManagement/ToggleCoupon/ToggleCoupon';
import useCouponStyle from './sellManagement/useCouponStyle/UseCouponStyle';
import dataStatistics from './dataStatistics/DataStatistics';
import helpCenter from './helpCenter/HelpCenter';

import noMatch from '../public/404';

const {Content, Footer} = Layout;


export default class StoreItem extends Component {

    constructor(props){
        super(props);
        // console.log(props)
    }

    state = {
        collapsed: localStorage.getItem("zyoyo_SiderCollapsed") === "true",
    };

    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        }, function () {
            localStorage.setItem("zyoyo_SiderCollapsed", this.state.collapsed);
        });
    };

    componentDidMount() {
        //保存Sider收缩
        if (localStorage.getItem("zyoyo_SiderCollapsed") === null) {
            localStorage.setItem("zyoyo_SiderCollapsed", false);
        }
    }

    render() {
        const {collapsed} = this.state;
        const {location} = this.props;
        let name;
        let shopName;
        var storeUser2 = getStorage("mspa_user2").toJson();
        var storeMsgStorage = getStorage("storeMsgStorage").toJson();
        if (storeUser2 === null) {
            return <Redirect to="/login"/>
        } else {
            if(storeMsgStorage === null){
                return <Redirect to="/store"/>
            }else{
                shopName = storeMsgStorage.shopName
            }
            name = storeUser2.username;
        }

        return (
            <Layout className="ant-layout-has-sider" style={{height: '100%'}}>
                <SiderCustom collapsed={collapsed} path={location.pathname}/>
                <Layout>
                    <HeaderCustom collapsed={collapsed} toggle={this.toggle} username={name} shopName={shopName}/>
                    <Content style={{margin: '0 16px'}}>
                        <Switch>
                            <Route exact path={'/storeItem/storeManagement'} component={storeManagement} />
                            <Route exact path={'/storeItem/goodsManagement'} component={goodsManagement} />
                            <Route exact path={'/storeItem/goodsManagement/toggleDish'} component={toggleDish} />
                            <Route exact path={'/storeItem/goodsManagement1'} component={goodsManagement1} />
                            <Route exact path={'/storeItem/sellManagement'} component={sellManagement} />
                            <Route exact path={'/storeItem/sellManagement/ToggleCoupon'} component={ToggleCoupon} />
                            <Route exact path={'/storeItem/sellManagement/useCouponStyle'} component={useCouponStyle} />
                            <Route exact path={'/storeItem/dataStatistics'} component={dataStatistics} />
                            <Route exact path={'/storeItem/helpCenter'} component={helpCenter} />
                            
                            {/*<Route exact path={'/app/header/Calendars'} component={Calendars} />
                            <Route exact path={'/app/chart/echarts'} component={Echarts} />
                            <Route exact path={'/app/richText'} component={RichText} />
                            <Route exact path={'/app/upload'} component={UploadEditor} /> */}
                            <Route component={noMatch} />
                        </Switch>
                    </Content>
                    {/* <Footer style={{textAlign: 'center'}}>
                        MSPA ©2017-2019 Created by QS
                    </Footer> */}
                </Layout>
            </Layout>
        );
    }
}