import React, { Component } from 'react';
import { getDish,deleteDish  } from '../../../config/getData';
import { getStorage} from '../../../config/mUtils';
import { Card, Row, Col, Modal, Button, message, Table, Tag, Input, Select, Pagination } from 'antd';
import history from '../../public/history';
import './GoodsManagement.scss';

export default class sellManageTable extends Component {
    constructor(props) {
        super(props);

        this.state = {
            getDishParams:{ //请求数据
                pageNum:1,//当前页
                pageSize:10,//页大小
                dishId:"", //商品id/查询条件
                orderBy:"", //排序
            },
            totalElements:"",//所有数据
            totalPages:"",//所有页
            initTableData:[],
            columns:[{
                align:"center",
                title: '商品',
                dataIndex: 'dishName',
                render: (dishName,item)=>{
                    // console.log(item);
                    var showImg = item.dishImageList[0].imagePath ? item.dishImageList[0].imagePath : "";
                    // console.log(showImg)
                    return (
                        <span>
                                <img src={showImg} alt={item.dishName} className="showImg" ></img>
                                <span>{item.dishName}</span>
                        </span>
                    )
                }
            }, {
                align:"center",
                title: '商品编号',
                dataIndex: 'dishId',
            }, {
                align:"center",
                title: '价格($)',
                dataIndex: 'dishPrice',
            }, {
                align:"center",
                title: '规格',
                dataIndex: 'dishUnit',
            }, {
                align:"center",
                title: '推荐数',
                dataIndex: 'goodNum',
                sorter: true,
                // sorter: (a, b) => {
                //     console.log(a)
                //     console.log(b)
                //     return b.goodNum - a.goodNum 
                // },
            },{
                align:"center",
                title: '操作',
                dataIndex: 'shopId',
                render: (shopId,item) => (
                    <span>
                        <Tag color={"#3f90f7"} onClick={(e)=>{this.addNewDish(item)}}>编辑</Tag>
                        <Tag color={"#f50"}  onClick={(e)=>{this.deleteTableDish(item)}}>删除</Tag>
                    </span>
                ),
            }]
            
        }
    }

    componentDidMount() {
        this.initPageData();
    }


    /**
     * 初始化界面数据
     */
    initPageData = () => {
        var getDishParams = this.state.getDishParams;
        getDishParams.shopId = getStorage("storeMsgStorage").toJson().shopId;
        // console.log(getDishParams)
        getDish(getDishParams).then((res)=>{
            var resData = res.data;
            // console.log(resData.data)
            if(resData.errorCode === "0000"){
                this.setState({
                    initTableData:resData.data.content,
                    totalElements:resData.data.totalElements,
                    totalPages:resData.data.totalPages,
                })
            }
        })
    }

    /**
     * 删除商品
     */
    deleteTableDish = (item) => {
        // console.log(item);
        var that = this;
        Modal.confirm({
            title: '是否确定删除商品: '+item.dishName+' ?',
            autoFocusButton:"cancel",
            okText:"确定",
            cancelText:"取消",
            maskClosable:true,
            width:500,
            centered:true,
            onOk() {
                deleteDish({dishId:item.dishId}).then((res)=>{
                    var resData = res.data;
                    if(resData.errorCode === "0000"){
                        that.initPageData();
                        message.success("成功删除商品: "+item.dishName+" !");
                    }else{
                        message.success(resData.errorMsg);
                    }
                })
            },
        });


    }

    /**
     * 新增优惠券
     */
    addNewDish = (item) => {
        // console.log(item);
        var path = {
            pathname:'/storeItem/goodsManagement/toggleDish',
            state:item,
        }
        history.push(path)
    }


    // 查询条件
    /**
     * 受控表单--优惠券change
     */
    dishNameOrIdChange(e) {
        // console.log(e.target.value);
        var getParams =  this.state.getDishParams;
        getParams.dishId = e.target.value
        this.setState({
            getDishParams: getParams
        })
    }

    /**
     * 根据商品名称编号查询
     */
    sureBtn = (e) => {
        this.initPageData();
    }

    // pagination
    /**
     * 切换表格页数
     */
    onShowSizeChange = (pageNum, pageSize) => {
        this.state.getDishParams.pageNum = parseInt(pageNum);
        this.state.getDishParams.pageSize = parseInt(pageSize);
        this.setState({
            getDishParams:this.state.getDishParams
        })
        this.initPageData();
    }

    /**
     * 选择页数 
     * quickJumper -- 回车键触发
     * @param pageNum:当前页 filters:筛选 sorter:排序
     */
    paginationOnChange = (pageNum,filters, sorter) => {
        // console.log( pageNum)
        var setPageNum = typeof pageNum == "number" ? pageNum : this.state.getDishParams.pageNum;
        //带有排序的切换界面
        if(typeof sorter == "object"){
            //升序(小-大)
            if(sorter.order == "ascend"){
                this.state.getDishParams.orderBy = "good_num asc";
            }else if(sorter.order == "descend"){
                this.state.getDishParams.orderBy = "good_num desc";
            }else{
                this.state.getDishParams.orderBy = "";
            }
        }
        //选择的页码
        this.state.getDishParams.pageNum = parseInt(setPageNum);
        this.setState({
            getDishParams:this.state.getDishParams
        })
        this.initPageData();
    }



    render() {
        const { initTableData,columns,totalPages,totalElements,getDishParams } = this.state;
        return (
            <div>
                <div className='goodsManageCont'>
                    <Card
                        title="商品管理"
                        extra={<div>
                            <Button type="primary" style={{ marginLeft: "10px" }} onClick={() => { this.addNewDish() }}>+新增商品</Button>
                        </div>}>
                        <Row gutter={60} className="goodsManagementCont">
                            <Col span={24}>
                                <Input placeholder="请输入商品名称或编号" style={{ width: 200, marginRight: "10px" }} value={this.state.getDishParams.dishId} onChange={(e) => { this.dishNameOrIdChange(e) }}></Input>
                                <Button type="primary" onClick={(e) => { this.sureBtn(e) }}>查询</Button>
                            </Col>

                            <Col span={24} className="goodsTable">
                                <Table columns={columns} 
                                    dataSource={initTableData}
                                    pagination={false}
                                    onChange={(e,filters,sorter) => { this.paginationOnChange(e,filters,sorter) }}
                                    rowKey="dishId" />
                            </Col>

                            <Col span={24} className="pagCont">
                                <span>共{totalElements}条数据,第 {getDishParams.pageNum} / {totalPages ? totalPages : "1"} 页</span>
                                <Pagination showSizeChanger 
                                    onShowSizeChange={this.onShowSizeChange}
                                    showQuickJumper 
                                    pageSizeOptions={["10","20","50","100"]}
                                    onChange={(e) => { this.paginationOnChange(e) }}
                                    defaultCurrent={Number(getDishParams.pageNum)} 
                                    defaultPageSize={Number(getDishParams.pageSize)} 
                                    total={Number(totalElements)} />
                            </Col>
                        </Row>
                    </Card>
                </div>
            </div>
        )
    }
}
