import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
import { Card, Row, List, Avatar, Modal, Button, Icon,Form, Input, Layout,message} from 'antd';
import {modNewUserName,modMerchantPsd} from '../../../config/getData';
import {getStorage,setStorage,removeStorage} from '../../../config/mUtils'
import history from '../../public/history';
import './AccountSetting.scss';

import HeaderCustom from '../../common/StoreListHeaderCustom';
import NewUserNameForm from './NewUserNameForm';

const FormItem = Form.Item;
const {Content, Footer} = Layout;

const data = [
    {
      title: '账户名',
      avatarIcon:"user",
      avatarColor:"#7A4BD8",
      description:'孙佳佳的包子铺',
      phone:false,
      dataFlag:"user"
    },
    {
      title: '登录密码',
      avatarIcon:"lock",
      avatarColor:"#58BFC1",
      description:'已设置，如忘记密码请联系客服，客服热线：',
      phone:true,
      dataFlag:"psd"
    }
  ];

class AccountSetting extends Component {
    constructor(props){
        super(props);
        // console.log(props)
        this.state = {
            userMsgPsdVisible:false, //修改密码弹出框
            newUserNameModal:{
                visible:false,   //服务设施弹出框
                newUserName:"", //账户名
            },
            // oldPassword:"",
            // newPassword:"",
            // confirmPassword:""

        }

        this.newusernameform = React.createRef();
    }

    componentDidMount(){
        this.initNewUserName();
    }

    /**
     * 初始化账户名称
     */
    initNewUserName = (res,flag)=>{
        // console.log(getStorage("mspa_user2").toJson());
        //修改账户名,退出到登录页
        console.log(res)
        console.log(flag)
        if(flag){
            var upadtadd = getStorage("mspa_user2").toJson();
            if(flag === "name"){
                upadtadd.username =  res;
                setStorage('zyoyoUser',upadtadd);//存储上个登录用户名
            }else if(flag === "psd"){
                upadtadd.password =  res;
                setStorage('zyoyoUser',upadtadd);//存储上个登录用户名
                return false;
            }
            Modal.warning({
                title: '修改用户名成功!请退出重新登录!',
                okText: '确定',
                centered:true,
                onOk() {
                    removeStorage("mspa_user2");
                    history.push('/login');
                }
            });
            return false;
        }

        this.handleUserModalCancel();
    }

    /**
     * 修改用户信息
     */
    changeAccountMsg = (e)=>{
        // 修改用户名
        if(e === "user"){
            this.setState({ newUserNameModal: {
                visible:true,
                newUserName:getStorage("mspa_user2").toJson().username
            }});
            this.newusernameform.current.resetFields();
        // 修改用户密码
        }else{
            this.setState({userMsgPsdVisible:true})
        }
    }

    /**
     * 取消修改用户名（弹出框）
     */
    handleUserModalCancel = ()=>{
        this.setState({ newUserNameModal: {
            visible:false,
            newUserName:getStorage("mspa_user2").toJson().username
        }});
    }

    // 确认修改用户名
    handleSubmitModUser = (values) => {
            // 调用接口
            modNewUserName(values).then((res)=>{
                // console.log(res)
                var resData = res.data;
                if(resData.errorCode === "0000"){
                    this.handleUserModalCancel();
                    this.initNewUserName(values.newUserName,"name");
                }
            })
    };

    // 确认修改用户密码
    handleSubmitModUserPsd = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            // console.log(err)
            // console.log(values)
            if (!err) {
                // 调用接口
                modMerchantPsd(values).then((res)=>{
                    // console.log(res)
                    var resData = res.data;
                    if(resData.errorCode === "0000"){
                        message.success("修改用户密码成功!");
                        this.userMsgModalCancel();
                        this.initNewUserName(values.newPassword,"psd");
                    }else{
                        message.error(resData.errorMsg);
                    }
                })               
            }
        });
    };

    //比较两次输入密码
    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('newPassword')) {
          callback('您输入的两个密码不一致!');
        } else {
          callback();
        }
      }
    
    

    /**
     * 取消修改用户名
     */
    userMsgModalCancel = (e) => {
        this.props.form.resetFields();
        this.setState({
            userMsgPsdVisible:false,
        });
    }


    render() {
        const { getFieldDecorator } = this.props.form;
        const {newUserName} = this.state.newUserNameModal;
        const formItemLayout = {
            labelCol: {
              xs: { span: 4 },
              sm: { span: 4 },
            },
            wrapperCol: {
              xs: { span: 20 },
              sm: { span: 20 },
            },
          };
        return (
            <div>
                <HeaderCustom username={newUserName}/>
                <Content style={{margin: '16px 0'}}>
                    <div className='accountSettingCont'>
                        <Card
                            title="账户设置">
                            <Row className="accountCont">
                            <List
                                itemLayout="horizontal"
                                dataSource={data}
                                renderItem={item => (
                                <List.Item>
                                    <List.Item.Meta
                                    avatar={<Avatar icon={item.avatarIcon} size={44} shape="square" style={{backgroundColor:item.avatarColor}} />}
                                    title={<span>{item.title}</span>}
                                    description={<span>{item.description}<a style={item.phone ? {display:"inline"} : {display:"none"}} href="tel:0773-4657839">0773-4657839</a></span>}
                                    />
                                    <div style={{color:"#1890FF",padding:"10px 30px"}} onClick={(e)=>{
                                        this.changeAccountMsg(item.dataFlag);
                                    }}>修改</div>
                                </List.Item>
                                )}
                            />,
                            </Row>
                        </Card>

                        {/* 修改用户信息(账户名)弹出框 */}
                        <NewUserNameForm
                            ref={this.newusernameform}
                            data={this.state.newUserNameModal} 
                            handleUserModalCancel={this.handleUserModalCancel} 
                            handleSubmitModUser={this.handleSubmitModUser}
                        ></NewUserNameForm>

                        {/* 修改用户信息(账户密码)弹出框 */}
                        <Modal
                        visible={this.state.userMsgPsdVisible}
                        onCancel={this.userMsgModalCancel}
                        width="600px"
                        title="修改登录密码"
                        centered={true}
                        className="userMsgModal"
                        footer={[
                            <Button key="back" onClick={this.userMsgModalCancel}>取消</Button>,
                            <Button key="submit" type="primary" onClick={this.handleSubmitModUserPsd}>确定</Button>,
                        ]}
                        >
                        <div>

                        <Form onSubmit={this.handleSubmitModUserPsd} {...formItemLayout} >
                            <FormItem label="输入旧密码">
                                {getFieldDecorator('oldPassword', {
                                    rules: [
                                        {required:true,message:'请输入旧密码！'},
                                        {pattern: /^[\w-*]{6,20}$/, message: '请输入正确的旧密码!' },
                                    ],
                                    // initialValue:this.state.oldPassword,
                                    validateFirst:true,
                                })(
                                    <Input  type="password" placeholder="请输入旧密码" name="oldPassword"></Input>
                                )}
                            </FormItem>
                            <Form.Item
                                label="输入新密码"
                                >
                                {getFieldDecorator('newPassword', {
                                    rules: [
                                        {required: true, message: '请输入正确的新密码!'},
                                        {pattern: /^[\w-*]{6,20}$/, message: '请输入正确的新密码!' },
                                        {validator: this.validateToNextPassword}
                                    ],
                                    // initialValue:this.state.newPassword,
                                })(
                                    <Input type="password"  placeholder="请输入新密码" name="newPassword" />
                                )}
                            </Form.Item>
                            <Form.Item
                                label="确认新密码"
                                >
                                {getFieldDecorator('confirmPassword', {
                                    rules: [
                                        {required: true, message: '请输入正确的新密码!'},
                                        {pattern: /^[\w-*]{6,20}$/, message: '请输入正确的新密码!' },
                                        {validator: this.compareToFirstPassword}],
                                    // initialValue:this.state.confirmPassword,
                                })(
                                    <Input type="password" placeholder="请再输入新密码" name="confirmPassword"  onBlur={this.handleConfirmBlur} />
                                )}
                            </Form.Item>
                        </Form>
                        </div>
                        </Modal>
                    </div>
                </Content>
            </div>
        )
    }
}

const accountSettingComp = Form.create()(AccountSetting);
export default accountSettingComp;
