export default class UseCouponStyle extends Component{


    constructor(props){
        super(props);
        console.log(props.location.state);

        this.state = {
            checkboxGroupOptions:[],
        }
    }   

    componentDidMount(){
        this.initPageData();
    }

    //初始化数据
    initPageData = () => {
        getMarketRule({shopId:getStorage("storeMsgStorage").toJson().shopId}).then((res)=>{
            console.log(res)
            var resData = res.data;
            console.log(resData.data);
            if(resData.errorCode === "0000"){
                if(resData.data.commonRulelist.length){
                    var commonRulelist = resData.data.commonRulelist;
                    commonRulelist.forEach((item,index)=>{
                        console.log(item);
                        console.log(index);
                        this.state.commonRulelist[index] = {}
                        this.state.commonRulelist[index].label = commonRulelist.ruleText;
                        this.state.commonRulelist[index].value = commonRulelist.ruleId;
                    })
                    
                    

                }   
            }
        })

    }

    backSellManagement = ()=>{
        history.push('/storeItem/sellManagement');

    }
  
      /**
       * 选择使用规则
       */
    userStyleSelect = (e) => {
        console.log(e);
    }

    render(){
        var {checkboxGroupOptions} = this.state;
    
        return (
            <div>
                <div className='useCouponStyle'>
                    <Card
                        title={<span><span style={{color:'#ADADAD'}}>营销管理 / </span><span>使用规则</span></span>}
                        extra={<div>
                                  <Button onClick={()=>(this.backSellManagement())}>取消</Button>
                                  <Button type="primary" style={{marginLeft:"10px"}} onClick={()=>{this.addNewCoupon()}}>保存</Button>
                               </div>}>
                        <Row className="userStyleCont">
                            <Col span={24}>
                            <Checkbox.Group style={{ width: '100%' }}  className="usmcCheckBoxCont" defaultValue={["A"]}
                                      onChange={(e)=>{this.userStyleSelect(e)}} options={checkboxGroupOptions}>
                            </Checkbox.Group>
                            </Col>
                        </Row>
                    </Card>
                    <Card
                        title={<span><span>其他使用规则</span><span style={{color:'#ADADAD',fontSize:'12px'}}>（如选项中没有符合要求的规则，可以自定义添加）</span></span>}
                        className="otherUserStyle">
                        <Row className="userStyleCont">
                            <Col span={24} className="usmcAddStyleCont">
                                <Input placeholder="其他优惠券规则"></Input>
                                <Button>添加</Button>
                            </Col>
                        </Row>
                    </Card>

                </div>
            </div>
        )
    }


}