/**
 * 存储localStorage
 */
export const setStorage = (name,content) => {
    if(!name) return;
    if(typeof content !== "string"){
        content = JSON.stringify(content);
    }
    window.localStorage.setItem(name,content);
}

/**
 * 获取localStorage
 */
export const getStorage = (name) => {
    if(!name) return;
    var returnData = window.localStorage.getItem(name);
    return {
        toString(){
            return returnData;
        },
        toJson(){
            var tryObj = {};
            try {
                tryObj = JSON.parse(returnData) === null ? "" : JSON.parse(returnData);
            } catch (e) {
                tryObj = null;
            }
            return tryObj;
        },
        toNumber(){
            return parseFloat(returnData);
        }
    }
}

/**
 * 删除localStorage
 */
export const removeStorage = (name) => {
    if(!name) return;
    window.localStorage.removeItem(name);
}