import React, { Component } from 'react';

import { Modal, Button,Input, Form } from 'antd';

class DescribeCom extends Component {
    constructor(props){
        super(props);
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        this.props.form.validateFields((err, values) => {
            console.log(err);
            console.log(values);
            if (!err) {
                that.props.handleSubmitDevice(values);
            }
        })
    }

    /**
     * 取消设施（弹出框）
     */
    handleDeviceModalCancel = ()=>{
        this.setState({
            visible:false
        });
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        const { visible,newDeviceData} = this.props.data;


        return (
            <div>
                    {/* 自定义服务设施弹出框 */}
                    <Modal
                        visible={visible}
                        title="添加自定义服务设施"
                        onCancel={this.props.handleDeviceModalCancel}
                        width="600px"
                        className="copyStoreModal"
                        centered={true}
                        footer={[
                            <Button key="back" onClick={this.props.handleDeviceModalCancel}>取消</Button>,
                            <Button key="submit" type="primary" onClick={this.handleSubmit}>
                            确定
                            </Button>,
                        ]}
                        >
                        <div >
                        <Form onSubmit={this.handleSubmit} ref="describe">
                            <Form.Item>
                            {getFieldDecorator('longDescribe', {
                                rules: [{
                                    required: true, message: '请输入正确的服务设施!',
                                },{
                                    pattern: /^[^0-9\s]{1,10}$/, message:'请输入除数字、空格以外的字符(10个字以内)!',
                                }],
                                initialValue: newDeviceData,
                            })(
                                <Input type="text" placeholder="请输入自定义服务设施" />
                            )}
                            </Form.Item>
                        </Form>
                        </div>
                    </Modal>


            </div>
        )
    }

}

export default Form.create({ name: 'describe' })(DescribeCom)