/**
 * 登录并获取token验证 
 */
export const loginApi  = (msg) => {
    var loginUrl = "/oauth/token?grant_type=password&scope=webBrower&username="+msg.username+"&password="+msg.password
    return $http.post(loginUrl,msg)
}

//------------------------店铺接口---------------------------------
/**
* 查询店铺列表
*/
export const getShopOverview  = (msg) => $http.get("/merchant/shop/overview")

/**
* 店铺上线 
*/
export const shopOnline  = (msg) => $http.post("/merchant/shop/online",msg)

/**
* 复制店铺 
*/
export const shopCopy  = (msg) => $http.post("/merchant/shop/copy",msg)

/**
* 查询店铺详情
* @param {shopId:string} msg 
*/
export const getShopDetail  = (msg) => $http.get("/merchant/shop/detail",{params:msg})

/**
 * 更新店铺信息
 */
export const updateShop  = (msg) => $http.put("/merchant/shop",msg)

/**
 * 查询店铺美食分类
 */
export const getFoodsStyle  = (msg) => $http.get("/merchant/style",msg)

/**
 * 查询店铺地区分类
 */
export const getAreaStyle  = (msg) => $http.get("/merchant/area",msg)

/**
 * 查询店铺二级标签()
 * @param (labelType:string 二级标签类型 0：服务设施; 1：店铺标签) 
 */
export const getSecondLabels  = (msg) => $http.get("/merchant/label/secondLabels",{params:msg})

//------------------------商品接口---------------------------------
