import React, { Component } from 'react';
import OSS from 'ali-oss';
import moment from 'moment';
import { Card, Row, Col, Icon, Avatar, Modal, Button, Checkbox, message, Table, Divider, Tag, Input, Select, Pagination, Form, Tooltip, Cascader, AutoComplete, DatePicker, Upload, Spin, Alert } from 'antd';
import './OssUploadImg.scss';

let client = "";

/**
 * props 
 *      --> ImgDatas //初始化界面数据
 *      --> formatParam {
 *              uploadImgLen:number, //最多上次图片数量
 *              maskCont:{
 *                  isShow //是否显示提示
 *                  text    //提示文本
 *                 }, 
 *              folder:string //存储文件位置
 *           }
 *      --> onChangeUploadImg //数据改变事件
 */

export default class OssUploadImg extends Component {
    constructor(props) {
        super(props);
        console.log(props)
        this.state = {
            fileList:[], //所有数据列表
            loading: false,
            dragItem:{}, //拖拽数据
            nowDragItem:{}, //目标拖拽数据
        };
    }

    componentDidMount() {
        client = new OSS({
            region: 'oss-us-west-1',
            accessKeyId: 'LTAICdHS3UrXF0jt',
            accessKeySecret: '6W3Ks3AVctu7h3Qd8wtJeYqNmcMdTE',
            bucket: 'zhuanyoyo',
            secure: true
        });
        // console.log(client)
    }

    //每次更改props刷新组件
    componentWillReceiveProps(nextProps){
        // console.log(nextProps)
        // 初始化店铺数据1,obj 具有首图样式;2,string 单张图片
        if(nextProps.ImgDatas){
            if(nextProps.ImgDatas instanceof Object){
                this.initObjDatas(nextProps);
            }else if(typeof nextProps.ImgDatas === "string"){
                this.initStrDatas(nextProps);
            }
        }
    }

    //初始化object格式界面数据(有首图,多图)
    initObjDatas = (nextProps)=>{
        var propsImgDatas = nextProps.ImgDatas;
        // console.log(propsImgDatas)
        propsImgDatas.map((item,index)=>{
            // 设置首图
            if(item.type === 1){
                propsImgDatas.splice(propsImgDatas.indexOf(item),1);
                propsImgDatas.unshift(item);
            }
            item.isShowCloseCircle = "none";
            return item;
        })
        this.setState({
            fileList:propsImgDatas
        })
    }
    //初始化string格式界面数据(单图)
    initStrDatas = (nextProps) =>{
        this.setState({
            fileList:[{
                url:nextProps.ImgDatas,
                isShowCloseCircle:"none"
            }]
        })
    }

    ossUpload = (file)=>{
        // console.log(file);
        var that = this;
        const path = that.props.formatParam.folder || 'BmanagementPlatform/uploadImg'; //图片存储文件夹
        const uploadImgFile = file; //上传文件
        const Name = uploadImgFile.name;
        const suffix = Name.substr(Name.indexOf(".")); //图片格式后缀
        const obj = moment().format('YYYYMMDDHHmmss')+Math.round((Math.random() + 1) * 1000000);
        // console.log(obj)
        const storeAs = path+'/'+obj+suffix; // 命名为: 路径+时间戳+图片格式后缀
        that.setState({loading:true});

        client.multipartUpload(storeAs,uploadImgFile).then(function(result){
            if (result.res.status === 200) {
                // 限制最多上传图片数
                if(that.state.fileList.length < that.props.formatParam.uploadImgLen){
                    var resultResCont = result.res.requestUrls[0];
                    var resultUrl = (resultResCont.indexOf("?") != -1) ? resultResCont.split("?")[0] :resultResCont; //获取上传的图片地址
                    var resultObj = { url: resultUrl, isShowCloseCircle:"none", type:0}; //封装每一张图片数据
                    that.state.fileList.push(resultObj);    // 添加到图片文件列表
                    // console.log(that.state.fileList.length)
                        that.setState({
                            fileList:that.state.fileList,
                            loading:false
                        })   
                        that.onChangeUpload();             
                }
                return false;
            } else {
                
            }
        })

    }

    /**
     * 删除上传图片
     */
    DeleteUploadImg = (item)=>{
        console.log(item);
        console.log(this.state.fileList);
        this.state.fileList.forEach((itemOne,index)=>{
            if(itemOne.url === item.url){
                this.state.fileList.splice(index,1);
            }
        })
        this.setState({fileList:this.state.fileList});
        this.onChangeUpload();
    }

    /**
     * 鼠标进入事件
     */
    uploadImgMouseEnter = (item)=>{
        this.state.fileList.map((itemOne,index)=>{
            if(itemOne.url === item.url){
                itemOne.isShowCloseCircle = "block";
            }
            return itemOne;
        })
        console.log(this.state.fileList)
        this.setState({fileList:this.state.fileList});
    }

    /**
     * 鼠标离开事件
     */
    uploadImgMouseLeave = (item)=>{
        this.state.fileList.map((itemOne,index)=>{
            if(itemOne.url === item.url){
                itemOne.isShowCloseCircle = "none";
            }
            return itemOne;
        })
        console.log(this.state.fileList)
        this.setState({fileList:this.state.fileList});
    }

    /**
     * 上传图片每次数据改变
     */
    onChangeUpload = ()=>{
        this.props.onChangeUploadImg(this.state.fileList);
    }

    /**
     * 开始拖拽
     */
    uploadImgOnDragStart = (item,e)=>{
        this.state.dragItem = item;
    }

    /**
     * 拖拽元素在目标区域移动
     */
    uploadImgOnDragOver = (item,e)=>{
        e.preventDefault();
    }

    /**
     * 手指释放鼠标按键事件
     */
    uploadImgOnDrag = (item,e)=>{
        this.state.nowDragItem = item;
    }

    /**
     * 拖拽事件结束
     */
    uploadImgOnDragEnd = (item,e)=>{

        var dragItem = this.state.dragItem;//上一个元素
        var nowDragItem = this.state.nowDragItem;//目标元素

        var prevItemIndex = this.state.fileList.indexOf(dragItem); //上一个元素在数组中的索引
        var nowItemIndex = this.state.fileList.indexOf(nowDragItem); //目标元素在数组中的索引 

        // 删除上个元素
        this.state.fileList.splice(prevItemIndex,1);
        // 把拖拽元素放到目标元素之前
        this.state.fileList.splice(nowItemIndex,0,item);

        this.setState({fileList:this.state.fileList});
        this.onChangeUpload();
        // console.dir(this.state.fileList)
        this.state.dragItem = {}; //拖拽数据置空
        this.state.nowDragItem = {}; //目标拖拽数据置空
    }

    render() {
        const {fileList,loading} = this.state;
        const {uploadImgLen,maskCont} = this.props.formatParam;
        // ant-Upload 组件属性
        const props = {
            accept:"image/jpg,image/jpeg,image/svg,image/png",
            listType:"picture-card",
            beforeUpload:(file)=>{
                // console.log(file)
                const isLt1M = file.size < 1048576;
                if (!isLt1M) {
                    message.error('Image must smaller than 1MB!');
                    return false;
                }
                this.ossUpload(file); //自定义oss上传
                return false;
            },
            fileList:[],
            multiple:true
        }

        //上传图片按钮
        const uploadButton = (
            <div className="uploadImgBtn">
                  <Icon type="plus" />
                 <div className="ant-upload-text">添加图片</div>
            </div>       
        );

        // console.log(fileList)
        // 展示图片列表
        const fileListDom = fileList.map((item,index)=>
                <div key={item.url} className="uploadImgListCont"  
                     onMouseEnter={(e)=> {this.uploadImgMouseEnter(item,e)}} 
                     onMouseLeave={(e)=> {this.uploadImgMouseLeave(item,e)}}
                     onDragStart={(e)=> {this.uploadImgOnDragStart(item,e)}} //开始拖拽
                     onDragOver={(e)=> {this.uploadImgOnDragOver(item,e)}} //拖拽元素在目标区域移动
                     onDrop={(e)=> {this.uploadImgOnDrag(item,e)}} //手指释放鼠标按键事件
                     onDragEnd={(e)=> {this.uploadImgOnDragEnd(item,e)}} //拖拽事件结束
                     >
                    <img src={item.url} alt={item.url} className="ullImg" />
                    <Icon  type="close-circle" theme="filled" className="ullDeleteBtn" onClick={(e)=> {this.DeleteUploadImg(item,e)}} style={{display:item.isShowCloseCircle}}></Icon>
                    {index === 0 && item.isShowCloseCircle === "block" && maskCont.isShow ? <div className="maskTips">{maskCont.text}</div> : ""}
                </div>
        )
        // console.log(fileListDom)

        return (
            <div className="ossContent"> 
                <Row>
                    <Col span={24}>
                        <div className="clearfix">
                            {fileList.length > 0 ? fileListDom : ''} 
                            {fileList.length < uploadImgLen ? <Upload {...props}>
                                {!loading ? uploadButton : ''}
                                {loading ? <Spin tip="图片上传中..." className="loadSpin"></Spin> : ''}
                            </Upload> : ""}
                        </div>
                    </Col>
                </Row>

            </div>
        )
    }




}
