import React, { Component } from 'react';
import {loginApi} from '../../config/getData';
import {setStorage,getStorage} from '../../config/mUtils';
import '../../style/login.scss';
import { Form, Icon, Input, Button, Checkbox, message, Spin, Modal } from 'antd';
const FormItem = Form.Item;

let initData = {
    username:"",
    password:"",
    remember:false
}

function info(){
    Modal.info({
        title:"忘记密码",
        cancelText:"sss",
        okText:"我知道了",
        maskClosable:true,
        width:500,
        centered:"center",
        content:(
            <div>
                 <p>如忘记密码请联系客服，客服热线：
                     <span>
                        <a href="tel:0773-4657839">0773-4657839</a>
                    </span>
                </p>
            </div>
        )
    })
}

class Login extends Component {
    constructor(props){
        super(props);
        this.state = {
            isLoding:false,
            initData:initData
        };
    }

    componentDidMount(){
        // 设置防抖动的键盘事件登录
        var timeoutflag = null;
        document.body.addEventListener("keyup",(e)=>{
            if (window.event) {
                e = window.event;
            }
            let code = e.charCode | e.keyCode;
            if (Number(code) === 13) {
                if(timeoutflag != null){
                    return;
                }
                timeoutflag=setTimeout(()=>{
                    this.handleSubmit(e);
                    clearTimeout(timeoutflag);
                },10);
            }
        })

        // 根据是否记住密码，动态显示密码
        var storeZyoyoUser = getStorage("zyoyoUser").toJson();
        console.log(storeZyoyoUser);
        if (storeZyoyoUser) {
            initData = Object.assign({},storeZyoyoUser);
            if(!initData.remember){
                initData.password = "";
            }
        }
        // console.log(initData)
        this.setState({
            initData:initData
        })
    }

    componentWillUnmount(){
        document.body.removeEventListener("keyup",()=>{});
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                var reqData = {
                    grant_type:"password",
                    scope:"webBrower",
                    username:values.username,
                    password:values.password
                    // userId:"82"
                }
                console.log(reqData);
                // 登录api
                loginApi(reqData).then(function (response) {
                    console.log(response);
                    var resData = response.data;
                    if(resData.access_token){
                        that.setState({
                            isLoding: true,
                        });
                        setStorage('mspa_user2',Object.assign(resData,values));//存储用户信息
                        setStorage('zyoyoUser',Object.assign(resData,values));//存储上个登录用户名
                        message.success('login successed!'); //成功信息
                        setTimeout(function() { //延迟进入
                            that.props.history.push({pathname:'/store',state:resData.data});
                        }, 1000);                        
                    }else{
                        message.error(resData.errorMsg); //失败信息
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            this.state.isLoding?<Spin size="large" className="loadingSpin" />:
            <div className="login">
                <div className="login-form">
                    <div className="login-logo">
                        <div className="login-name">转YOYO商家管理后台</div>
                    </div>
                    <div className="login-flatMsg">互联网020平台，全行业全场景解决方案</div>
                    <div className="login-title">账户密码登录</div>
                    <Form onSubmit={this.handleSubmit} style={{maxWidth: '370px'}}>
                        <FormItem>
                            {getFieldDecorator('username', {
                                rules: [{required:true,message:'请输入用户名！'},
                                    { pattern: /^[\w-\u4e00-\u9fa5]{1,40}$/, message: '请输入正确的用户名！!' },
                                ],
                                initialValue:this.state.initData.username,
                                validateFirst:true
                            })(
                                <Input prefix={<Icon type="user" style={{ fontSize: 14 }} />} placeholder="用户名" allowClear />
                            )}
                        </FormItem>
                        <FormItem>
                            {getFieldDecorator('password', {
                                rules: [{ required: true, message: '请输入密码!' },
                                        { min: 6, max:20, message: '密码长度为6-20位!' },
                                        { pattern: /^[\w-*]{6,20}$/, message: '请输入正确的密码！!' },
                                ],
                                initialValue:this.state.initData.password,
                                validateFirst:true
                            })(
                                <Input prefix={<Icon type="lock" style={{ fontSize: 14 }} />} type="password" placeholder="密码" allowClear />
                            )}
                        </FormItem>
                        <FormItem style={{marginBottom:'0'}}>
                            {getFieldDecorator('remember', {
                                valuePropName: 'checked',
                                initialValue: this.state.initData.remember,
                            })(
                                <Checkbox>记住密码</Checkbox>
                            )}
                            <div className="login-form-forgot"  style={{float:'right'}} onClick={info}>
                                忘记密码
                            </div>
                            <Button type="primary" htmlType="submit" className="login-form-button" style={{width: '100%'}}>
                                登录
                            </Button>
                        </FormItem>
                    </Form>
                </div>
            </div>
        );
    }
}

const loginComp = Form.create()(Login);
export default loginComp;