import React, { Component } from 'react';

import { Card, Row, Col, Icon, Avatar, Modal, Button, Checkbox, message, Table, Divider, Tag, Input, Select, Pagination, Form, Tooltip, Cascader, AutoComplete, DatePicker } from 'antd';
import { getStorage } from '../../../../config/mUtils'
import { addDish,updateDish } from '../../../../config/getData';
import history from '../../../public/history';
import OssUploadImg from '../../../common/ossUpload/OssUploadImg';
import './ToggleDish.scss';

const { Option } = Select;
const AutoCompleteOption = AutoComplete.Option;
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;

// 上传商品图片要求设置
const uploadShopImgFormatParms = { 
    uploadImgLen: 9, 
    maskCont: { isShow: true, text: "主图" },
    folder:"BmanagementPlatform/shopImg" };

class ToggleDish extends Component {
    constructor(props) {
        super(props);
        

        this.state = {
            isAddNewDish:true,
            toggleDishTitle:"新增商品",
            updateDishData:{},
            dishName:"",
            dishPrice:"",
            dishUnit:"",
            dishImageList: [
            //     {//商品图片
            //     imagePath:"https://zhuanyoyo.oss-us-west-1.aliyuncs.com/test1/test/201904221114501702820.png",
            //     isPriamry:"1"
            // }
        ], 
        };
    }

    componentDidMount(){
        //修改店铺
        if(this.props.location.state){
            // console.log(this.props.location.state);
            this.initPageData(this.props.location.state);
            this.setState({
                isAddNewDish:false,
                toggleDishTitle:"修改商品",
                updateDishData:this.props.location.state
            })
        }
    }

    /**
     * 初始化界面数据
     */
    initPageData = (initData) => {
        // console.log(initData)
        // console.log(JSON.parse(initData.dishImages))
        JSON.parse(initData.dishImages).forEach((item,index)=>{
            this.state.dishImageList[index] = {};
            this.state.dishImageList[index].url = item.imagePath;
            this.state.dishImageList[index].type = item.isPriamry;
        })
        this.setState({
            dishImageList:this.state.dishImageList
        })
    }

    /**
     * 上传店铺图片每次数据改变
     */
    onChangeUploadShopImg = (res) => {
        // 设置第一张为首图
        res.map((item,index)=>{
            if(index === 0){
                item.type = 1;
            }else{
                item.type = 0;
            }
        })
        // console.log(res);
        this.state.dishImageList = res;
    }

    //取消按钮
    backGoodsManagement = () => {
        history.push('/storeItem/goodsManagement');
    }

    /**
     * 提交数据到后台
     */
    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        // console.log(this.state.initData)
        this.props.form.validateFields((err, values) => {
            // console.log(err);
            // console.log(values);
            if(!err){
                //判断是否已经上传商品图片
                // console.log(this.state.dishImageList.length)
                var pushDishImageList = [];
                if(this.state.dishImageList.length){
                    this.state.dishImageList.forEach((item,index)=>{
                        pushDishImageList[index] = {
                            imagePath:item.url,
                            isPriamry:item.type
                        }
                    })
                }else{
                    message.error("请上传店铺图片!")
                }
                // console.log(pushDishImageList)
                values.dishImageList = pushDishImageList;
                values.shopId = getStorage("storeMsgStorage").toJson().shopId;
                if(this.state.isAddNewDish){
                    // 新增商品
                    addDish(values).then((res)=>{
                        // console.log(res)
                        var resData = res.data;
                        if(resData.errorCode === "0000"){
                            message.success("新增商品成功!");
                            this.backGoodsManagement();
                        }else{
                            message.error(resData.errorMsg);
                        }
                    })

                }else{
                    // 修改商品
                    values.dishId = this.state.updateDishData.dishId;
                    updateDish(values).then((res)=>{
                        // console.log(res)
                        var resData = res.data;
                        if(resData.errorCode === "0000"){
                            message.success("修改商品成功!");
                        }else{
                            message.error(resData.errorMsg);
                        }
                    })
                }
            }
        })
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const {toggleDishTitle} = this.state;
        

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 20 },
            },
        };


        return (
            <div>
                <div className='toggleDish'>
                    <Card
                        title={<span><span style={{ color: '#ADADAD' }}>营销管理 / </span><span>{toggleDishTitle}</span></span>}
                        extra={<div>
                            <Button onClick={() => (this.backGoodsManagement())}>取消</Button>
                            <Button type="primary" style={{ marginLeft: "10px" }} onClick={(e) => { this.handleSubmit(e)  }}>保存</Button>
                        </div>}>
                        <Row gutter={60} className="toggleDishCont">
                            <Col span={24}>


                                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                    <Form.Item
                                        label="商品名称"
                                    >
                                    <Row gutter={20} >
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('dishName', {
                                            rules: [{
                                                required: true, message: '请输入商品名称!',
                                            },{
                                                max:20, message: '商品名称20字以内!',
                                            }],
                                            initialValue: this.state.updateDishData.dishName,
                                        })(
                                            <Input type="text" placeholder="请输入商品名称"  />
                                        )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">20字以内</Col>
                                    </Row>
                                    </Form.Item>
                                    <Form.Item
                                        label="价格"
                                        style={{marginBottom:"0"}}
                                    >
                                        <Row gutter={20}>
                                            <Col xs={24} sm={24} md={24} lg={6}>
                                            <Form.Item>
                                                {getFieldDecorator('dishPrice', {
                                                    rules: [{
                                                        required: true, message: '请输入商品价格',
                                                    },{
                                                        pattern: /^[\d\.]+[\d]{0,10}$/, message: '请输入正确的商品价格!', 
                                                    }],
                                                    initialValue: this.state.updateDishData.dishPrice,
                                                })(
                                                    <Input type="text" placeholder="请输入商品价格" addonAfter="$"/>
                                                )}
                                            </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={24} md={24} lg={6}>
                                            <Form.Item>
                                                {getFieldDecorator('dishUnit', {
                                                    rules: [{
                                                        required: true, message: '请输入商品单位',
                                                    },{
                                                        max:10, message: '请输入正确的商品单位!',
                                                    }],
                                                    initialValue: this.state.updateDishData.dishUnit,
                                                })(
                                                    <Input type="text" placeholder="请输入商品单位" />
                                                )}
                                            </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={24} md={24} lg={6}>单位如:份、盒等</Col>
                                            </Row>
                                    </Form.Item>
                                  

                                    <Form.Item
                                        label="商品图片"
                                        className="hasBottomDotted"
                                    >
                                        <Row gutter={20} className="storeItemCont">
                                            <Col span={24}>
                                                <OssUploadImg 
                                                ImgDatas={this.state.dishImageList} 
                                                onChangeUploadImg={this.onChangeUploadShopImg} 
                                                formatParam={uploadShopImgFormatParms}></OssUploadImg>
                                            </Col>
                                            <Col span={24} className="ossUploadImgTips">
                                                尺寸建议:750x750(正方形模式)和jpg格式,大小1M以下,最多9张(可拖拽图片调整显示顺序)
                                            </Col>
                                        </Row>
                                    </Form.Item>

                                </Form>

                            </Col>
                        </Row>
                    </Card>
                </div>
            </div>
        )
    }


}

export default Form.create({ name: 'ToggleDishForm' })(ToggleDish)