import React, { Component } from 'react';
import { Layout, Icon, Menu } from 'antd';
// import { Link } from 'react-router-dom';
import history from '../public/history';

const { Header } = Layout;
const SubMenu = Menu.SubMenu;

export default class HeaderCustom extends Component{
    constructor(props){
        super(props);
        this.state = {
            arrowsShowDown:true
        }
        this.logout = this.logout.bind(this);
    }

    // 登出
    logout(){
        localStorage.removeItem("mspa_user2");
        history.replace('/login');
    }

    // 切换店铺
    changeStore(){
        history.replace("/store");
    }

    //账户设置
    setting(){
        history.replace("/store/accountSetting");
    }

    // 下拉菜单显示隐藏事件
    subMenuChange(e){
        console.log(e.length)
        e.length ? this.setState({arrowsShowDown:false}) : this.setState({arrowsShowDown:true}); 
    }

    render(){
        return(
            <Header style={{padding: 0}} className="header">
                <div className="logoNameCont">
                    <div className="logo"/> 
                    <div className="platformName">商家管理后台</div>
                </div>
                <Menu
                    className="headMenu"
                    theme="dark"
                    mode="horizontal"
                    selectable = {false}
                    subMenuCloseDelay = {0.1}
                    onOpenChange = {(e)=>{this.subMenuChange(e)}}
                >
                    <SubMenu 
                        className="userSetting"
                        key="userSetting"
                        title={
                        <span>
                            <Icon type="user" style={{color: '#1DA57A' }}/>
                            <span>{this.props.username}</span>
                            <Icon type= {this.state.arrowsShowDown ?  "down" : "up"} style={{fontSize:"12px",marginLeft: '6px' }}></Icon>
                        </span>}>
                        <Menu.Item key="interation"  className="logout"><Icon type="interation"></Icon>
                            <span onClick={this.changeStore} >切换店铺</span>
                        </Menu.Item>
                        <Menu.Item key="setting" className="logout"><Icon type="setting"></Icon>
                            <span onClick={this.setting} >账户设置</span>
                        </Menu.Item>
                        <Menu.Item key="logout"  className="logout"><Icon type="logout"></Icon>
                            <span onClick={this.logout} >退出登录</span>
                        </Menu.Item>
                    </SubMenu>
                </Menu>
            </Header>
        )
    }
} 