import React, { Component } from 'react';

import { Card, Row, Col, Button, message, Input, Select,Form } from 'antd';
import { getStorage } from '../../../../config/mUtils'
import { addMarketCoupon,updateMarketCoupon } from '../../../../config/getData';
import history from '../../../public/history';
import './ToggleCoupon.scss';

const { Option } = Select;


class ToggleCoupon extends Component {
    constructor(props) {
        super(props);
        console.log(props);

        this.state = {
            couponType:"0", //优惠券类型
            isAddNewCoupon:true, 
            toggleCouponTitle:"新增优惠券",
            updateCouponData:{},
        };
    }

    componentDidMount(){
        //修改店铺
        if(this.props.location.state){
            // console.log(this.props.location.state);
            this.props.location.state.shareTimes = this.props.location.state.shareTimes.toString();
            this.setState({
                isAddNewCoupon:false,
                toggleCouponTitle:"修改优惠券",
                updateCouponData:this.props.location.state,
                couponType:this.props.location.state.couponType
            })
        }
        
    }

    //选择优惠券类型
    changeCouponType = (e) => {
        // console.log(e);
        this.state.couponType = e;
        this.state.updateCouponData.couponValue = "";
        this.state.updateCouponData.shareTimes = "";
        this.setState({
            couponType:e,
            updateCouponData:this.state.updateCouponData
        })
        this.props.form.resetFields();
    }

    //动态验证优惠券内容
    valudateCouponValue = (rule, value, callback) => {
        if (this.state.couponType == "0") {
            if(!/^[\d\.]{1,6}$/.test(value)){
                callback('请输入代金券正确的优惠内容!');
            }else{
                callback();
            }
        } else if(this.state.couponType == "1"){
            if(!/^[\d\.]{1,6}$/.test(value)){
                callback('请输入折扣券正确的优惠内容!');
            }else{
                callback();
            }
        }else if(this.state.couponType == "2"){
            if(!/^[\w\-\u4e00-\u9fa5]{1,10}$/.test(value)){
                callback('请输入赠品券正确的优惠内容!');
            }else{
                callback();
            }
        }else{
            callback();
        }
      }

    //取消
    backSellManagement = () => {
        history.push('/storeItem/sellManagement');

    }

    //保存
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            // console.log(values);
            if (!err) {
                // console.log('Received values of form: ', values);
                values.shopId = getStorage("storeMsgStorage").toJson().shopId;
                if(this.state.isAddNewCoupon){
                    //新增优惠券
                    addMarketCoupon(values).then((res)=>{
                        console.log(res)
                        var resData = res.data;
                        if(resData.errorCode === "0000"){
                            message.success("新增优惠券成功!");
                            this.backSellManagement();
                        }else{
                            message.error(resData.errorMsg);
                        }
                    })
                }else{
                    //修改优惠券
                    values.couponId = this.state.updateCouponData.couponId;
                    updateMarketCoupon(values).then((res)=>{
                        // console.log(res)
                        var resData = res.data;
                        if(resData.errorCode === "0000"){
                            message.success("修改优惠券成功!");
                            this.backSellManagement();
                        }else{
                            message.error(resData.errorMsg);
                        }
                    })
                }

            }
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        var {couponType,toggleCouponTitle,updateCouponData} = this.state;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 20 },
            },
        };



        return (
            <div>
                <div className='ToggleCouponCont'>
                    <Card
                        title={<span><span style={{ color: '#ADADAD' }}>营销管理 / </span><span>{toggleCouponTitle}</span></span>}
                        extra={<div>
                            <Button onClick={() => (this.backSellManagement())}>取消</Button>
                            <Button type="primary" style={{ marginLeft: "10px" }} onClick={(e) => {this.handleSubmit(e)}}>保存</Button>
                        </div>}>
                        <Row gutter={60} className="ToggleCoupon">
                            <Col span={24}>


                                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                    <Form.Item
                                        label="优惠券类型"
                                    >
                                    <Row gutter={20}>
                                        <Col span={12}>
                                            {getFieldDecorator('couponType', {
                                                rules: [{
                                                    required: true, message: '请选择优惠券类型!',
                                                }],
                                                initialValue:couponType
                                            })(
                                                <Select placeholder="选择优惠券类型" onChange={(e)=>{this.changeCouponType(e)}}>
                                                    <Option value="0">代金券</Option>
                                                    <Option value="1">折扣券</Option>
                                                    <Option value="2">赠品券</Option>
                                                </Select>
                                            )}
                                        </Col>
                                    </Row>
                                    </Form.Item>
                                    <Form.Item
                                        label="优惠内容"
                                        required
                                    >
                                        <Row gutter={20}>
                                            <Col span={12}>
                                                {getFieldDecorator('couponValue', {
                                                    rules: [{
                                                        validator: this.valudateCouponValue,
                                                    }],
                                                    initialValue:updateCouponData.couponValue
                                                })(
                                                    <Input type="text" placeholder="输入优惠内容" />
                                                )}
                                            </Col>
                                            <Col 
                                                style={{display : (couponType == "0" ? "block" : "none")}}
                                                span={12}
                                                >元代金券面值</Col>
                                            <Col 
                                                style={{display : (couponType == "1" ? "block" : "none")}}
                                                span={12}
                                                >%折扣券比例</Col>
                                            <Col 
                                                style={{display : (couponType == "2" ? "block" : "none")}}
                                                span={12}
                                                >每份赠品内容</Col>
                                        </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label="转发数量"
                                    >
                                    <Row gutter={20}>
                                        <Col span={12}>
                                                {getFieldDecorator('shareTimes', {
                                                    rules: [
                                                        { required: true, message: '请输入转发次数!', whitespace: true },
                                                        {pattern: /^[\d]{0,3}$/, message: '请输入正确的转发次数!'}, 
                                                    ],
                                                    initialValue:updateCouponData.shareTimes
                                                })(
                                                    <Input type="text" placeholder="输入转发次数" />
                                                )}
                                        </Col>
                                        <Col span={12}>
                                            次转发兑换一次
                                        </Col>
                                    </Row>
                                    </Form.Item>


                                    {/* <Form.Item
                                        label="开始时间"
                                    >
                                        {getFieldDecorator('startTime', {
                                            rules: [{ required: true, message: 'startTime!' }],
                                        })(
                                            <DatePicker />
                                        )}
                                    </Form.Item>

                                    <Form.Item
                                        label="结束时间"
                                    >
                                        {getFieldDecorator('endTime', {
                                            rules: [{ required: true, message: 'endTime!' }],
                                        })(
                                            <DatePicker />
                                        )}
                                    </Form.Item> */}

                                    {/* <Form.Item>
          <Button type="primary" htmlType="submit">Register</Button>
        </Form.Item> */}
                                </Form>

                            </Col>
                        </Row>
                    </Card>
                </div>
            </div>
        )
    }


}

export default Form.create({ name: 'ToggleCouponForm' })(ToggleCoupon)