import React, { Component } from 'react';
import { Layout, Menu, Icon } from 'antd';
import { Link } from 'react-router-dom';

const { Sider } = Layout;
// const SubMenu = Menu.SubMenu;
function formatProps(val){
    var copyNextProps = JSON.parse(JSON.stringify(val));
    // 子路由右侧导航actived
    if(copyNextProps.path.indexOf("/storeItem/sellManagement") === 0){
        copyNextProps.path = "/storeItem/sellManagement"
    }else if(copyNextProps.path.indexOf("/storeItem/goodsManagement") === 0){
        copyNextProps.path = "/storeItem/goodsManagement"
    }       
    return copyNextProps;
}

export default class SiderCustom extends Component{
    constructor(props){
        super(props);
        const { collapsed } = props;
        this.state = {
            collapsed:collapsed,
            firstHide: true, //第一次先隐藏暴露的子菜单
            selectedKey: '', //选择的路径
            openKey: '', //打开的路径（选择的上一层）
        }
    }
    componentDidMount() {
        this.setMenuOpen(formatProps(this.props));
    }
    componentWillReceiveProps(nextProps) {
        this.onCollapse(nextProps.collapsed);
        this.setMenuOpen(formatProps(nextProps));
    }
    setMenuOpen = props => {
        const {path} = props;
        this.setState({
            openKey: path.substr(0, path.lastIndexOf('/')),
            selectedKey: path
        });
    };
    onCollapse = (collapsed) => {
        this.setState({
            collapsed,
            firstHide: collapsed,
        });
    };
    menuClick = e => {
        this.setState({
            selectedKey: e.key
        });
    };
    openMenu = v => {
        this.setState({
            openKey: v[v.length - 1],
            firstHide: false,
        })
    };
    render(){
        const { collapsed, firstHide, openKey, selectedKey } = this.state;
        return(
            <Sider
            width="220px"
            collapsed={collapsed}
            collapsedWidth="0"
            trigger={null}
            >   
                <div className="logoNameCont">
                    <div className="logo"/> 
                    <div className="platformName" style={collapsed?{display:'none'}:{display:'block'}}>商家管理后台</div>
                </div>
                <Menu
                    theme="dark"
                    mode="inline"
                    selectedKeys={[selectedKey]}
                    onClick={this.menuClick}
                    onOpenChange={this.openMenu}
                    openKeys={firstHide ? null : [openKey]}
                >
                    <Menu.Item key="/storeItem/storeManagement">
                        <Link to={'/storeItem/storeManagement'}><Icon type="home" /><span>店铺管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key={"/storeItem/goodsManagement"}>
                        <Link to={"/storeItem/goodsManagement"}><Icon type="appstore" /><span>商品管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key="/storeItem/sellManagement">
                        <Link to={'/storeItem/sellManagement'}><Icon type="money-collect" /><span>营销管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key="/storeItem/dataStatistics">
                        <Link to={'/storeItem/dataStatistics'}><Icon type="fund" /><span>数据统计</span></Link>
                    </Menu.Item>  
                    <Menu.Item key="/storeItem/helpCenter">
                        <Link to={'/storeItem/helpCenter'}><Icon type="bulb" /><span>帮助中心</span></Link>
                    </Menu.Item>                                       
                    {/* <SubMenu key="/storeItem/chart"
                    title={<span><Icon type="area-chart" /><span>图表</span></span>}
                    >
                        <Menu.Item key="/storeItem/chart/echarts">
                            <Link to={'/storeItem/chart/echarts'}><span>echarts</span></Link>
                        </Menu.Item>
                    </SubMenu> */}
                </Menu>
            </Sider>
        )
    }
}