import history from '../components/public/history';
import Base64  from 'base-64';
import {getStorage,removeStorage} from './mUtils';
import axios from "axios";
import {message} from "antd";

//配置axios
// axios.defaults.headers.get['Content-Type'] = 'application/json';
//axios.defaults.headers.post['Content-Type'] = 'application/json';

var server =  axios.create({
    // 生产版本开启本地域名
    // baseURL:'https://www.95cfun.top',
    baseURL:'http://139.196.73.183:8765/back',
    timeout:40000,
    // 设置请求头token和请求数据格式
    headers:{'Content-Type':'application/json'}

})

// 拦截请求
server.interceptors.request.use((config)=>{
    // console.log(config);
    // 拦截登录请求,并设置请求头
    if(config.url.indexOf( "/oauth/token") !== -1){
        config.headers.Authorization = "Basic " + Base64.encode("client:secret");
    }else{
        // console.log(getStorage("zyoyoUser").toJson());
        var access_token = getStorage("zyoyoUser").toJson().access_token ? getStorage("zyoyoUser").toJson().access_token : "";
        if(access_token){
            config.headers.Authorization ="Bearer "+access_token;
        }
    }
    return config;
},(error)=>{
    console.log(error);
})

// 拦截返回
server.interceptors.response.use((res)=>{
    console.log(res)
    // var resCode = response.data.errorCode;
    if(res.data){
        if(res.data.errorCode == "00001"){
            message.error("用户登录过期,请重新登录!");
            removeStorage("mspa_user2");
            history.push('/login');
        }
    }
    return res;
},(error)=>{
    if(error.response){
        console.log(error.response.status)
        // token验证失败，退出登录
        if(error.response.status === 401){
            message.error("用户登录过期,请重新登录!")
            removeStorage("mspa_user2");
            history.push('/login');
        }if(error.response.status === 504){
            message.error("status:504,Gateway Time-out")
        }
    }
    return Promise.reject(error);
})

export const $http = server;


